<template>
  <NavBar />
  <div class="flex justify-center flex-col md:flex-row py-1 md:py-4">
    <div class=" px-4">
      <CardAvatar
        title="ยอดศาลายา: "
        :sales="Number(10000).toLocaleString()"
        :bgImage="imageBackground.branch1"
        :avatarImage="imageAvatar.branch1"
        :temperature="temperatureBranch1"
        :humidity="humidityBranch1"
        :pm25="pmBranch1"
      />
    </div>
    <div class=" px-4">
      <CardAvatar
        title="ยอดคาสเดย์: "
        :sales="Number(15000).toLocaleString()"
        :bgImage="imageBackground.branch2"
        :avatarImage="imageAvatar.branch2"
        :temperature="temperatureBranch2"
        :humidity="humidityBranch2"
        :pm25="pmBranch2"
      />
    </div>
  </div>
</template>

<script>
import NavBar from "@/components/NavBar";
import CardAvatar from "@/components/CardAvatar";
import { ref, onMounted, onUnmounted } from "vue";
import firebase from "firebase/app";
import "firebase/database";
import "firebase/storage";

const firebaseConfig = {
  apiKey: "AIzaSyBxhuIoX-oA8Nh803UgEMBd79hzH8Cj5hM",
  authDomain: "drugstore2328245.firebaseapp.com",
  databaseURL: "https://drugstore2328245.firebaseio.com",
  projectId: "drugstore2328245",
  storageBucket: "drugstore2328245.appspot.com",
  messagingSenderId: "616672198177",
  appId: "1:616672198177:web:cdc5bc87559d5ba303ad60",
  measurementId: "G-WRYZ030G0W",
};

const firebaseApp = firebase.initializeApp(firebaseConfig);

export default {
  name: "Home",
  components: { NavBar, CardAvatar },

  setup() {
    let h1 = firebaseApp.database().ref("Humidity");
    let h2 = firebaseApp.database().ref("Humidity_b2");
    let p1 = firebaseApp.database().ref("PM25");
    let p2 = firebaseApp.database().ref("PM25_b2");
    let t1 = firebaseApp.database().ref("Temperature");
    let t2 = firebaseApp.database().ref("Temperature_b2");

    const imageBackground = ref({
      branch1: require("../assets/images/salaya.jpg"),
      branch2: require("../assets/images/classday.jpg"),
    });

    const imageAvatar = ref({
      branch1: require("../assets/images/profile_1.jpg"),
      branch2: require("../assets/images/profile_2.jpg"),
    });

    const humidityBranch1 = ref("");
    const humidityBranch2 = ref("");
    const pmBranch1 = ref("");
    const pmBranch2 = ref("");
    const temperatureBranch1 = ref("");
    const temperatureBranch2 = ref("");

    onMounted(() => {

      h1.on("value", (ss) => {
        humidityBranch1.value = ss.val().toFixed(1);
      });
      h2.on("value", (ss) => {
        humidityBranch2.value = ss.val().toFixed(1);
      });
      p1.on("value", (ss) => {
        pmBranch1.value = ss.val().toFixed(2);
      });
      p2.on("value", (ss) => {
        pmBranch2.value = ss.val().toFixed(2);
      });
      t1.on("value", (ss) => {
        temperatureBranch1.value = ss.val().toFixed(1);
      });
      t2.on("value", (ss) => {
        temperatureBranch2.value = ss.val().toFixed(1);
      });
    });

    onUnmounted(() => {
      h1.off();
      h2.off();
      p1.off();
      p2.off();
      t1.off();
      t2.off();
    });

    return {
      humidityBranch1,
      humidityBranch2,
      pmBranch1,
      pmBranch2,
      temperatureBranch1,
      temperatureBranch2,
      imageBackground,
      imageAvatar
    };
  },
};
</script>
